.App {
  background-color: #282c34;
  font-size: max(calc(1.25vw), 1rem);
  color: white;
  text-align: left;
  min-height: 100vh;
  width: 100%;
}

.section__textAlignEnd {
  text-align: end;
}

.section__textAlignCenter {
  text-align: center;
}

.section__textAlignStart {
  text-align: start;
}

.block__characterTop--margin {
  margin-left: 1vw;
}

.section__navbar {
  margin-top: 3vh;
  margin-left: 0.5vw;
  max-width: 8vw;
}

.section__body {
  width: 100%;
}

.center {
  float: right;
  width: 70%;
}

.section__App--row {
  min-height: 0vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.section__App--column {
  display: flex;
  flex-direction: column;
  /* margin-left: 100px; */
  align-items: center;
}

/* Chrome, Safari, Edge, Opera */

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

input[type="number"] {
  -moz-appearance: textfield;
}
.table__table {
  border: 1px solid white;
  border-collapse: collapse;
}

.table__th,
.table__td {
  border: 1px solid white;
  padding-left: 5px;
  padding-bottom: 5px;
}
